
.banner {
    color: white;
    width: 100%;
    position: relative;
    text-align: center;
    height: 30em;
    overflow: hidden; /* Добавлено, чтобы скрыть возможные переполнения */
}

.banner::before {
    margin: -5px;
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 105%;
    height: 105%;
    background-image: url('../../images/chrome_banner_5184x3456.jpg');
    background-size: cover;
    background-position: center;
    filter: blur(3px); /*  размытие */
    opacity: 0.9; /*  полупрозрачность */
    z-index: -1; /*  фон позади содержимого */
}

/* text */
.banner h1 {
    font-size: 5em;
}
.banner p {
    font-size: 1.5em;
    margin-bottom: 3.9em;
}

/* button CSS */
.button-85 {
  padding: 1em 4em;
  border: none;
  outline: none;
  color: rgb(255, 255, 255);
  background: #111;
  cursor: pointer;
  position: relative;
  z-index: 0;
  border-radius: 10px;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}

.button-85:before {
  content: "";
  background: linear-gradient(
    45deg,
    #ff0000,
    #ff7300,
    #fffb00,
    #48ff00,
    #00ffd5,
    #002bff,
    #7a00ff,
    #ff00c8,
    #ff0000
  );
  position: absolute;
  top: -2px;
  left: -2px;
  background-size: 400%;
  z-index: -1;
  filter: blur(5px);
  -webkit-filter: blur(5px);
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  animation: glowing-button-85 20s linear infinite;
  transition: opacity 0.3s ease-in-out;
  border-radius: 10px;
}

@keyframes glowing-button-85 {
  0% {
    background-position: 0 0;
  }
  50% {
    background-position: 400% 0;
  }
  100% {
    background-position: 0 0;
  }
}

.button-85:after {
  z-index: -1;
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background: #222;
  left: 0;
  top: 0;
  border-radius: 10px;
  
}